.about-me-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px 0 0 0;
}

.about-me-parent {
  max-width: 1000px;
  width: 100%;
}

.about-me-card {
  display: flex;
  width: 100%;
  box-shadow: 0 0px 20px -2px #1f2235;
}

.about-me-profile {
  width: 50%;
  background-image: url("../../assets/AboutMe/aboutMePhoto.JPG");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.about-me-details {
  width: 48%;
  text-align: justify;
  padding: 30px;
}

.about-me-description {
  font-size: 15px;
}

.about-me-highlights {
  margin: 80px 0;
}

.highlight-heading {
  font-family: "Poppoins Bold";
  font-size: 18px;
  margin: 0 0 5px 0;
}
