.highlight {
  display: flex;
  flex-direction: column;
  font-family: "Poppoins Bold";
  margin: 10px 0;
  font-size: 15px;
}

.highlight-header {
  display: flex;
  align-items: center;
  font-family: "Poppoins Bold";
}

.highlight-blob {
  height: 12px;
  width: 12px;
  margin-right: 10px;
  background-color: #ff5823;
}

.highlight-heading {
  font-weight: bold;
}

.highlight-description {
  margin-left: 22px;
}

.about-me-options .highlighted-btn {
  margin: 0 0 0 30px;
}

@media only screen and (max-width: 1110px) {
  .about-me-parent {
    width: 80%;
  }
}
@media only screen and (max-width: 898px) {
  .about-me-profile {
    display: none;
  }
  .about-me-details {
    width: 100%;
  }
}

@media only screen and (max-width: 466px) {
  .about-me-options {
    display: flex;
    flex-direction: column;
  }
  .about-me-options .highlighted-btn {
    margin: 24px 0 0 0 !important;
  }

  .about-me-options button {
    width: 100%;
  }
}
